@import "../../Dashboard/Dashboard.module.scss";
.modalFormWindow {
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 50vh;
  background: #313541;
  color: #ffffff;
}
.heading {
  display: flex;
  justify-content: space-between;
}

.datepicker {
  background-color: transparent;
  font-family: $font;
  border: 1px solid $background;
  color: $text-color;
  outline: none;
  padding: calculateRem(10);
}

.head_actions {
  display: grid;
  grid-template-columns: 2fr 3fr;
  justify-items: flex-end;
  &_action {
    display: flex;
    input {
      margin-left: calculateRem(10);
    }
  }
}
